import { Suspense } from "react";
import { Link } from "react-router-dom/";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/css/image-gallery.css";

import takePicture from "../../assets/svg/take-picture.svg";
import createAdvert from "../../assets/svg/create-advert.svg";
import sellOnline from "../../assets/svg/sell-online.svg";
import IndexNavbar from "../../components/Navbar/IndexNavbar";

const FallBack = () => {
  return (
    <div className="flex mx-auto  justify-center mt-36 w-2/3 bg-white py-6 ">
      <h4 className="text-black text-2xl font-bold">Loading ... .. .</h4>
    </div>
  );
};
interface Props {}

const images = [takePicture, createAdvert, sellOnline];

const slideImages = () => {
  let res = [];
  for (let i = 0; i < images.length; i++) {
    res.push({
      original: images[i],
      thumbnail: "",
    });
  }
  return res;
};

const RegisterPage = (props: Props) => {
  const handleRegistration = (e: any) => {
    e.preventDefault();
  };

  return (
    <Suspense fallback={<FallBack />}>
      <IndexNavbar />

      <div className="flex justify-center items-centerselect-none">
        <div className="flex mt-20 rounded-md  lg:mt-24 bg-white  w-5/6 lg:w-4/6 mb-10">
          <div className="hidden md:block flex-1 flex-col rounded-l-md px-5  my-auto text-white font-bold text-4xl bg-contain md:border-r-0   bg-no-repeat bg-center shadow-sm">
            <ImageGallery
              items={slideImages()}
              showBullets={false}
              showIndex={false}
              showFullscreenButton={false}
              showNav={false}
              showPlayButton={false}
              showThumbnails={false}
              autoPlay={true}
            />
          </div>

          {/* FORM ONRIGHT SIDE */}
          <div className="flex-1 flex-col md:border-l-0 rounded-md md:rounded-l-none px-8 py-4 shadow-sm">
            <h2 className="text-md pb-2"> Create Account</h2>
            <h3 className="text-sm text-gray-400 pb-4">it's free and easy</h3>
            <form
              onSubmit={(e) => handleRegistration(e)}
              className="flex-col items-center"
            >
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="name"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Your name
                </label>
                <input
                  placeholder="Enter your name"
                  type="text"
                  className="flex-grow w-full h-10 text-sm px-4 mb-2 transition duration-200 bg-white border border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline"
                  id="name"
                  name="name"
                />
              </div>
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="phone"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Phone number
                </label>
                <input
                  placeholder="Enter hone number"
                  type="text"
                  className="flex-grow w-full h-10 text-sm px-4 mb-2 transition duration-200 bg-white border border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline"
                  id="phone"
                  name="phone"
                />
              </div>
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="email"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Email
                </label>
                <input
                  placeholder="Enter email"
                  type="email"
                  className="flex-grow w-full h-10 text-sm px-4 mb-2 transition duration-200 bg-white border border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline"
                  id="email"
                  name="email"
                />
              </div>
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="password"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Password
                </label>
                <input
                  placeholder="Enter secure password"
                  type="password"
                  className="flex-grow w-full h-10 text-sm px-4 mb-2 transition duration-200 bg-white border border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline"
                  id="password"
                  name="password"
                />
              </div>
              <div className="mb-1 sm:mb-2">
                <label
                  htmlFor="confirm password"
                  className="inline-block mb-1 font-normal text-sm"
                >
                  Confirm password
                </label>
                <input
                  placeholder="Confirm password"
                  type="password"
                  className="flex-grow w-full h-10 text-sm px-4 mb-2 transition duration-200 bg-white border border-gray-300 rounded shadow-sm appearance-none focus:border-deep-purple-accent-400 focus:outline-none focus:shadow-outline"
                  id="confirm password"
                  name="confirm password"
                />
              </div>
              <div className="mt-4 mb-4 md:mb-4 flex justify-center">
                <button
                  type="submit"
                  className="inline-flex items-center justify-center w-full h-12  px-2 font-normal tracking-wide text-white transition duration-200 rounded shadow-md bg-orange-600 hover:bg-white hover:text-orange-600 hover:border-orange-500 hover:border-2 focus:shadow-outline focus:outline-none focus:shadow-none"
                >
                  <svg
                    className="w-6 h-6 mr-0.5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M18 9v3m0 0v3m0-3h3m-3 0h-3m-2-5a4 4 0 11-8 0 4 4 0 018 0zM3 20a6 6 0 0112 0v1H3v-1z"
                    />
                  </svg>
                  Create Account
                </button>
              </div>
              <p className="text-xs text-gray-600 sm:text-sm ">
                Already have an account ?
                <Link
                  to="/login"
                  className="text-md font-medium tracking-wide text-orange-500 space-x-0"
                >
                  <span> Login </span>
                </Link>
              </p>
            </form>
          </div>
        </div>
      </div>
    </Suspense>
  );
};

export default RegisterPage;
